//copy text

function copyData(containerid) {
  let range = document.createRange();
  range.selectNode(containerid); //changed here
  window.getSelection().removeAllRanges();
  window.getSelection().addRange(range);
  document.execCommand("copy");
  window.getSelection().removeAllRanges();
}

function copyDataTwo(containeridTwo) {
  let range = document.createRange();
  range.selectNode(containeridTwo); //changed here
  window.getSelection().removeAllRanges();
  window.getSelection().addRange(range);
  document.execCommand("copy");
  window.getSelection().removeAllRanges();
}

var prevScrollpos = window.pageYOffset;
window.onscroll = function() {
  var currentScrollPos = window.pageYOffset;
  if (prevScrollpos > currentScrollPos) {
    console.log("1")
    $( ".menu__btn" ).addClass('_active')
  } else {
    console.log("2")
    $( ".menu__btn" ).removeClass('_active')
  }
  prevScrollpos = currentScrollPos;

  //fix bug сьезжает тултип на айфоне если виден и начинаешь скролить
  const tooltipOne = document.querySelector('.address__popup.address__popup--one-copy');
  const tooltipTow = document.querySelector('.address__popup.address__popup--tow-copy');
  if(tooltipOne && tooltipOne.classList.contains('active')) {
    $(".address__popup.address__popup--one-copy").removeClass('active');
  }
  if(tooltipTow && tooltipTow.classList.contains('active')) {
    $(".address__popup.address__popup--tow-copy").removeClass('active');
  }
}


let popupOpen = document.querySelectorAll(".popupOpen")
let popupClosed = document.querySelectorAll(".popup__btn")
let windowpopup = document.querySelector(".popup")

popupOpen.forEach((item)=> {
  item.addEventListener("click", function (e) {
    e.preventDefault()
    $("html,body").css("overflow-y","hidden");
    hideWindiwsMenu();
    windowpopup.style.display = "flex";
  })
})
popupClosed.forEach((item)=> {
  item.addEventListener("click", function () {
    windowpopup.style.display = "none";
    $("html,body").css("overflow-y","visible");
  })
})

